* {
	box-sizing: border-box;
}

body {
	font-family: 'Segoe UI', sans-serif;
	font-style: normal;
	font-weight: 400;
	font-size: 20px;
	line-height: 27px;
	color: $desc-color;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

a {
	text-decoration: none;
}

.container {
	max-width: 1620px;
	margin: 0 auto;
	padding-right: 14px;
	padding-left: 14px;
}

.section__title {
	font-family: 'League Spartan', sans-serif;
	font-style: normal;
	font-weight: 600;
	font-size: 70px;
	line-height: 68px;
	color: $title-color;
	margin-top: 0;
	margin-bottom: 0;
}
.section__title-more {
	display: block;
	font-weight: 400;
}

.section__description {
	margin-bottom: 30px;
}

.btn {
	cursor: pointer;
	background: $active-gradient;
	font-style: normal;
	font-weight: 600;
	font-size: 24px;
	line-height: 32px;
	color: $active-color-text;
	box-shadow: $active-shadow;
	border: none;
	border-radius: 10px;
	padding: 16px 48px;
	transition: opacity .3s ease;
}
.btn:hover {
	opacity: .8;
}

@media (max-width: 1620px) {
	.container {
		max-width: 1200px;
	}
}

@media (max-width: 1400px) {

}

@media (max-width: 1200px) {
	.container {
		max-width: 1000px;
	}
	.section__title {
        font-size: 60px;
    }
}

@media (max-width: 992px) {
	body {
		font-size: 20px;
	}
	.container {
		max-width: 788px;
	}
	.section__description {
		margin-bottom: 42px;
	}
	.btn {
		font-size: 20px;
	}
}

@media (max-width: 768px) {
	.container {
		max-width: 100%;
	}
}

@media (max-width: 576px) {
	body {
		font-size: 16px;
		line-height: 24px;
	}
	.section__title {
        font-size: 40px;
		line-height: 1;
    }
	.section__description {
		margin-bottom: 42px;
	}
	.btn {
		font-size: 16px;
		padding: 12px 40px;
	}
}

@media (max-width: 400px) {
	.section__title {
        font-size: 30px;
		line-height: 1;
    }
}
