.hero {
    background-color: #f4fbf1;
    @include bg("../images/hero-bg.jpg");
    position: relative;
    padding-top: 262px;
    padding-bottom: 287px;
}

.hero__wrapper {
    max-width: 636px;
}

.hero__comment {
    position: absolute;
    width: 300px;
    padding-top: 20px;
    padding-right: 30px;
    padding-bottom: 42px;
    padding-left: 30px;
    z-index: 10;
}
.hero__comment::before {
    content: "";
    display: block;
    position: absolute;
    background-color: #FFFFFF;
    border-radius: 10px;
    box-shadow: 0px 9px 50px rgba(0, 0, 0, 0.17);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transform: skew(15deg);
    z-index: -1;
}

.hero__comment--one {
    bottom: 335px;
    right: 41%;
}
.hero__comment--two {
    bottom: 202px;
    right: 78px;
}

.hero__comment-header {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 10px;
}
.hero__comment-img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.hero__comment-name {
    font-family: 'Segoe UI';
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    color: #000000;
    margin-top: 0;
    margin-bottom: 0;
}

.hero__comment-ratings {
    display: flex;
    align-items: center;
}

.hero__comment-star {
    width: 12px;
    height: 12px;
    margin-right: 3px;
    @include bg("../images/comments-avatars/star.svg")
}

.hero__comment-description {
    font-size: 12px;
    line-height: 16px;
    color: #000000;
}
// MEDIA
@media (max-width: 1800px) {
    .hero__comment--one {
        bottom: 50%;
        right: 37%;
    }

    .hero__comment--two {
        bottom: 20%;
        right: 10%;
    }
}

@media (max-width: 1620px) {
    .hero__comment--one {
        bottom: 64%;
        right: 26%;
    }
}

@media (max-width: 1200px) {
    .hero {
		padding-top: 200px;
		padding-bottom: 200px;
	}
	.hero__comment--one {
		bottom: 75%;
	}
}

@media (max-width: 992px) {
    .hero {
        position: relative;
        padding-top: 100px;
        padding-bottom: 100px;
        z-index: 10;
    }
    .hero::before {
        content: "";
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        background-color: rgb(255 255 255 / 56%);
        width: 100%;
        height: 100%;
        z-index: 10;
    }
    .hero__comment {
        display: none;
    }
    .hero__wrapper {
        position: relative;
        text-align: center;
        max-width: 100%;
        padding: 40px;
        z-index: 100;
    }
}

@media (max-width: 768px) {
    .hero {
        padding-top: 50px;
        padding-bottom: 50px;
    }
}

@media (max-width: 576px) {
    .hero__wrapper {
        padding: 0;
    }
        .hero::before {
            background-color: rgb(255 255 255 / 80%);
    }
}