@font-face {
  font-family: "League Spartan";
  src: url("../fonts/league-spartan/LeagueSpartan-Regular.woff2") format("woff2"), url("../fonts/league-spartan/LeagueSpartan-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "League Spartan";
  src: url("../fonts/league-spartan/LeagueSpartan-SemiBold.woff2") format("woff2"), url("../fonts/league-spartan/LeagueSpartan-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Segoe UI";
  src: url("../fonts/segoe-ui/SegoeUI-Bold.woff2") format("woff2"), url("../fonts/segoe-ui/SegoeUI-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Segoe UI";
  src: url("../fonts/segoe-ui/SegoeUI.woff2") format("woff2"), url("../fonts/segoe-ui/SegoeUI.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  box-sizing: border-box;
}

body {
  font-family: "Segoe UI", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #545454;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1620px;
  margin: 0 auto;
  padding-right: 14px;
  padding-left: 14px;
}

.section__title {
  font-family: "League Spartan", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 70px;
  line-height: 68px;
  color: #86B472;
  margin-top: 0;
  margin-bottom: 0;
}

.section__title-more {
  display: block;
  font-weight: 400;
}

.section__description {
  margin-bottom: 30px;
}

.btn {
  cursor: pointer;
  background: linear-gradient(180.61deg, #98D67D 0.45%, #569F36 99.39%);
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  color: #fff;
  box-shadow: 0px 1px 20px rgba(134, 180, 114, 0.69);
  border: none;
  border-radius: 10px;
  padding: 16px 48px;
  transition: opacity 0.3s ease;
}

.btn:hover {
  opacity: 0.8;
}

@media (max-width: 1620px) {
  .container {
    max-width: 1200px;
  }
}
@media (max-width: 1200px) {
  .container {
    max-width: 1000px;
  }
  .section__title {
    font-size: 60px;
  }
}
@media (max-width: 992px) {
  body {
    font-size: 20px;
  }
  .container {
    max-width: 788px;
  }
  .section__description {
    margin-bottom: 42px;
  }
  .btn {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 100%;
  }
}
@media (max-width: 576px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
  .section__title {
    font-size: 40px;
    line-height: 1;
  }
  .section__description {
    margin-bottom: 42px;
  }
  .btn {
    font-size: 16px;
    padding: 12px 40px;
  }
}
@media (max-width: 400px) {
  .section__title {
    font-size: 30px;
    line-height: 1;
  }
}
.header {
  position: relative;
  background-color: #fff;
  background-image: url("../images/header-bg.png");
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: 0px 4px 37px rgba(0, 0, 0, 0.2);
  padding-top: 14px;
  padding-bottom: 15px;
  overflow: hidden;
  z-index: 100;
}

.container--header {
  max-width: 1648px;
  padding-right: 0;
  padding-left: 14px;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__nav-list {
  display: flex;
  align-items: center;
  gap: 78px;
  margin-top: 2px;
}

.logo {
  width: 138px;
  height: 108px;
  overflow: hidden;
}

.logo__img {
  width: 100%;
  height: 100%;
}

.header__nav-link {
  display: block;
  font-weight: 600;
  font-size: 28px;
  color: #000;
  padding-right: 10px;
  padding-left: 10px;
  transition: 0.3s color ease;
}

.header__nav-link:hover {
  color: #86B472;
}

.header__nav-link--active {
  font-size: 32px;
  letter-spacing: 1px;
  color: #86B472;
  position: relative;
  margin-top: -14px;
}

.header__nav-link--active::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 7px;
  background-color: #86B472;
  box-shadow: 0px 1px 20px rgba(134, 180, 114, 0.69);
  border-radius: 5px;
  bottom: -21px;
  left: 0;
}

@media (max-width: 1620px) {
  .container--header {
    max-width: 1200px;
    padding-right: 10px;
    padding-left: 10px;
  }
  .header__nav-link {
    font-size: 28px;
  }
  .header__nav-link--active {
    font-size: 30px;
    margin-top: 0;
  }
}
@media (max-width: 1400px) {
  .header__nav-link,
  .header__nav-link--active {
    font-size: 26px;
  }
  .header__nav-list {
    margin-top: 0;
    gap: 20px;
  }
}
@media (max-width: 1200px) {
  .container--header {
    max-width: 1000px;
  }
  .header__nav-link,
  .header__nav-link--active {
    font-size: 24px;
  }
  .header__nav-link--active:before {
    height: 5px;
    bottom: -15px;
  }
}
@media (max-width: 1024px) {
  .header__nav-list {
    gap: 40px;
  }
  .header__nav-link,
  .header__nav-link--active {
    font-size: 20px;
    padding-right: 0;
    padding-left: 0;
  }
}
@media (max-width: 992px) {
  .container--header {
    max-width: 788px;
  }
  .logo {
    width: 100px;
    height: auto;
  }
  .header__nav-list {
    gap: 28px;
  }
  .header__nav-link,
  .header__nav-link--active {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .container--header {
    max-width: 100%;
  }
}
@media (max-width: 724px) {
  .header {
    position: relative;
  }
  .header__nav {
    position: fixed;
    box-shadow: 0 4px 37px rgba(0, 0, 0, 0.2);
    width: 280px;
    top: 0;
    right: -100%;
    transition: right 0.3s ease;
    z-index: 100;
  }
  .header__nav-list {
    background-color: #fff;
    padding: 20px;
    flex-direction: column;
  }
  .header__nav--active {
    top: 0;
    right: 0;
    transition: right 0.3s ease;
  }
  .burger {
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    top: 50%;
    right: 48px;
    transform: translateY(-50%);
    z-index: 110;
  }
  .burger:before,
  .burger:after {
    content: "";
    display: block;
    width: 100%;
    height: 6px;
    background: linear-gradient(180.61deg, #98D67D 0.45%, #569F36 99.39%);
    position: absolute;
    border-radius: 5px;
    transition: transform 0.3s ease;
  }
  .burger:before {
    top: 0;
    left: 0;
  }
  .burger:after {
    content: "";
    bottom: 0;
    left: 0;
  }
  .burger__row {
    position: absolute;
    width: 100%;
    height: 6px;
    background: linear-gradient(180.61deg, #98D67D 0.45%, #569F36 99.39%);
    border-radius: 5px;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  .burger--active {
    top: 28px;
    right: 10px;
  }
  .burger--active .burger__row {
    display: none;
  }
  .burger--active.burger:before {
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
  }
  .burger--active.burger:after {
    bottom: 50%;
    transform: translateY(50%) rotate(-45deg);
  }
}
@media (max-width: 576px) {
  .header {
    background-image: none;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .logo {
    width: 80px;
  }
  .burger {
    right: 10px;
  }
  .header__nav {
    width: 100%;
  }
}
.hero {
  background-color: #f4fbf1;
  background-image: url("../images/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  padding-top: 262px;
  padding-bottom: 287px;
}

.hero__wrapper {
  max-width: 636px;
}

.hero__comment {
  position: absolute;
  width: 300px;
  padding-top: 20px;
  padding-right: 30px;
  padding-bottom: 42px;
  padding-left: 30px;
  z-index: 10;
}

.hero__comment::before {
  content: "";
  display: block;
  position: absolute;
  background-color: #FFFFFF;
  border-radius: 10px;
  box-shadow: 0px 9px 50px rgba(0, 0, 0, 0.17);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: skew(15deg);
  z-index: -1;
}

.hero__comment--one {
  bottom: 335px;
  right: 41%;
}

.hero__comment--two {
  bottom: 202px;
  right: 78px;
}

.hero__comment-header {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
}

.hero__comment-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.hero__comment-name {
  font-family: "Segoe UI";
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  color: #000000;
  margin-top: 0;
  margin-bottom: 0;
}

.hero__comment-ratings {
  display: flex;
  align-items: center;
}

.hero__comment-star {
  width: 12px;
  height: 12px;
  margin-right: 3px;
  background-image: url("../images/comments-avatars/star.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.hero__comment-description {
  font-size: 12px;
  line-height: 16px;
  color: #000000;
}

@media (max-width: 1800px) {
  .hero__comment--one {
    bottom: 50%;
    right: 37%;
  }
  .hero__comment--two {
    bottom: 20%;
    right: 10%;
  }
}
@media (max-width: 1620px) {
  .hero__comment--one {
    bottom: 64%;
    right: 26%;
  }
}
@media (max-width: 1200px) {
  .hero {
    padding-top: 200px;
    padding-bottom: 200px;
  }
  .hero__comment--one {
    bottom: 75%;
  }
}
@media (max-width: 992px) {
  .hero {
    position: relative;
    padding-top: 100px;
    padding-bottom: 100px;
    z-index: 10;
  }
  .hero::before {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0.56);
    width: 100%;
    height: 100%;
    z-index: 10;
  }
  .hero__comment {
    display: none;
  }
  .hero__wrapper {
    position: relative;
    text-align: center;
    max-width: 100%;
    padding: 40px;
    z-index: 100;
  }
}
@media (max-width: 768px) {
  .hero {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media (max-width: 576px) {
  .hero__wrapper {
    padding: 0;
  }
  .hero::before {
    background-color: rgba(255, 255, 255, 0.8);
  }
}